import { MessagingExtendedAttribute } from '@aspecto/opentelemetry-base';
import { Span } from '@opentelemetry/api';
import {
    SocketIoInstrumentationConfig,
    SocketIoHookFunction,
    SocketIoHookInfo,
} from 'opentelemetry-instrumentation-socket.io';

const setPayloadHook: SocketIoHookFunction = (span: Span, hookInfo: SocketIoHookInfo) =>
    span.setAttribute(MessagingExtendedAttribute.MESSAGING_PAYLOAD, JSON.stringify(hookInfo.payload));

export const aspectoSocketIoPluginConfig: SocketIoInstrumentationConfig = {
    traceReserved: false,
    emitHook: setPayloadHook,
    onHook: setPayloadHook,
};
